import React, { useState } from 'react';

function Converter() {
  const [input, setInput] = useState("123.8125");
  const [inputBase, setInputBase] = useState(10);
  const [outputBase, setOutputBase] = useState(2);

  const [result, setResult] = useState('');



  const convertBase = () => {
    console.clear();
    let outstring ="";
    //입력값 Base 진법이 맞는 지 확인
    //진법이 안 맞는 경우, base진법이 10진법으로 가정 후 진행 
    let baseType = inputBase;
    if(!checkBase(input , baseType)){
      console.log("base 진법 : "+baseType);
      console.log("입력값이 진법과 형태 오류 발생");
      if(checkBase(input , 10)){
        console.log("10 진법 가정시, 정상 동작");
        outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
          <strong>${baseType} 진법 사용 오류 -> 10진법 변환</strong> 
        </div><br/>`;
        baseType = 10;
      }else{
        console.log("10 진법 가정시, 비정상");
        console.log("종료");
        return "입력 숫자 확인 필요";
      }
    }

    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
											<strong>입력 값 (변수):</strong> <span style="color:#007bff;">${input}</span>
										</div>`;

    let nValue = baseToTargetBase(input, baseType, outputBase);
    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
										<strong>${outputBase}진수 변환 결과:</strong> <span style="color:#28a745;">${nValue}</span>
									</div>`;

    nValue = baseToTargetBase(nValue, outputBase, baseType);
    outstring += `<div style="margin-bottom: 15px; font-size: 1.2rem;">
										<strong>원복 (${baseType}진수):</strong> <span style="color:#dc3545;">${nValue}</span>
									</div>`;

    setResult(outstring);
  };

    // 진법 확인 (정규식)
    const checkBase = (value ,base) => {
       // 공백 제거
      const trimmedValue = value.trim();
      switch (base) {
        case 2: // 2진수 (0과 1로만 구성된 값 + 선택적 소수점)
          if (/^[01]+(\.[01]+)?$/.test(trimmedValue)) 
            return true;
          break;
        case 8: // 8진수 (0부터 7로 구성된 값 + 선택적 소수점)
          if (/^[0-7]+(\.[0-7]+)?$/.test(trimmedValue)) 
            return true;
          break;
        case 10: // 10진수 (0부터 9로 구성된 값 + 선택적 소수점)
          if (/^[0-9]+(\.[0-9]+)?$/.test(trimmedValue)) 
            return true;
          break;
      
        case 16: // 16진수 (0부터 9, A부터 F까지의 값 + 선택적 소수점)
          if (/^[0-9A-Fa-f]+(\.[0-9A-Fa-f]+)?$/.test(trimmedValue)) 
            return true;
          break;
        default:
          return false;
      }
    return false;
  };

  const baseToTargetBase = (nBaseNumber, base, targetBase) => {
		let [integerPart, fractionalPart] = nBaseNumber.split('.'); // 정수와 소수 분리
		let rtn = 0;
   
		console.log("      ***  시 작  ***");
		console.log("입력값 : "+nBaseNumber);

//-------------------------------------------------------------------------------------------------------------
// *** 1 정수부 계산 ***
//-------------------------------------------------------------------------------------------------------------

    if(0){
			//함수 사용
			let baseValue = parseInt(integerPart, base); // Base진법 -> 10진법 
			rtn = baseValue.toString(targetBase).toUpperCase();  // 10진법 -> targetBase진법 
			console.log("정수 (함수) : "+rtn);
		}else{ 
			//공식 계산
			let baseValue = integerPart;
			if(base != 10) //base진법 -> 10진법
				baseValue = baseToDecimal(baseValue , base);
			// 10진법 ->  targetBase 진법 
			rtn = decimalToBase(baseValue , targetBase);
			console.log("정수 (공식) : " + rtn);
		}
//-------------------------------------------------------------------------------------------------------------
// *** 2 소수부 계산 ***
//-------------------------------------------------------------------------------------------------------------
		if (fractionalPart !=null && fractionalPart.length >= 0) {
			//실수부는 무조건 공식 사용 
			let baseValue = fractionalPart; //실수 변환
			if(base != 10)  //base진법 -> 10진법 
				baseValue = baseToDecimalFraction(baseValue , base);
			//10진법 -> targetBase 진법 
			rtn += "." + decimalFractionToBase(baseValue , targetBase);

			console.log("정수 + 실수 : " + rtn);
		}
		console.log("----------------------------------");
		return rtn; 
  };

  const baseToDecimal = (nValue, base) => {
		const inValue = nValue;
		let rtn = 0; 

		//공식
		let nSum = 0;
		for (let i = 0; i < inValue.length; i++) {
				const decimalValue = parseInt(inValue[i], base); 
				// 현재 자릿수에 대한 거듭제곱
				const power = inValue.length - 1 - i; 
				nSum += decimalValue * Math.pow(base, power); // 전체 누적
		}
		rtn = nSum ;

    return rtn;
  };

  const decimalToBase = (nValue, base) => {

		const inValue = nValue; //변수값
		const inBase = base; // base진법
		let rtn = ''; 

		//공식으로 계산
		let baseValue = '';
		let subRtn = inValue;
		while (subRtn > 0) {
			const remainder = subRtn % inBase; // 나머지 계산
			baseValue = remainder.toString(inBase).toUpperCase() + baseValue; // 변환 및 누적
			subRtn = Math.floor(subRtn / inBase); // 몫으로 업데이트
		}

		rtn = baseValue; // 10진법 -> targetBase진법 
		console.log("-decimalToBase ( 10 진법 ->"+base+"진법 )"+rtn);
		return rtn; 
  };

  const baseToDecimalFraction = (nValue, base) => {
		const inValue = nValue;
		const inBase = base; // base진법
	
		let rtn = 0; 
		let subRtn = 0; // 10진법으로 변환 예정
		for (let i = 0; i < inValue.length; i++) {
			const decimalDigit = parseInt(inValue[i], inBase); // n진법 -> 10진법
			subRtn += decimalDigit * Math.pow(inBase, -(i + 1)); // 소수점 아래 계산;
		}
		rtn = subRtn.toString().split('.')[1];
	
		console.log("-baseToDecimalFraction  ("+inBase+"진법 -> 10 진법) => "+rtn);
		return rtn; //string : 소수점 자리만
  };

  const decimalFractionToBase = (nValue, base) => {
		let inValue = parseFloat("0."+nValue);//변수값
		const inBase = base; // base진법

    let rtnValue = ''; 
		let subRtn = ''; //글자로 저장
		let baseValue = '';
		let precisionLimit = 20; // 소수부 자릿수 제한 
		 //string 으로 변수 저장
		while (inValue > 0.0 && subRtn.length < precisionLimit) {
			inValue *= inBase;
			const fractionDigit = Math.floor(inValue); 
			baseValue += fractionDigit.toString(inBase).toUpperCase(); // 변환 및 누적
			subRtn += fractionDigit;
			inValue -= fractionDigit;
		}
		rtnValue = baseValue;
		console.log("-decimalFractionToBase ( 10 진법  ->"+base+"진법) => "+rtnValue);
    return rtnValue; //string : 소수점 자리만
  };

  return (
    <div style={styles.container}>
      <h1 style={styles.title}>진수 변환</h1>
      <p style={styles.description}>2진수, 8진수, 10진수, 16진수 변환 가능</p>
      
      <input
        type="text"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        placeholder="Enter number"
        style={styles.input}
      />
      
      <div style={styles.selectContainer}>
        <div>
          <label style={styles.label}>변수 기본 진법:</label>
          <select
            value={inputBase}
            onChange={(e) => setInputBase(parseInt(e.target.value))}
            style={styles.select}
          >
            <option value="2">Binary (2진법)</option>
            <option value="8">Octal (8진법)</option>
            <option value="10">Decimal (10진법)</option>
            <option value="16">Hexadecimal (16진법)</option>
          </select>
        </div>
        <div>
          <label style={styles.label}>변환할 진법:</label>
          <select
            value={outputBase}
            onChange={(e) => setOutputBase(parseInt(e.target.value))}
            style={styles.select}
          >
            <option value="2">Binary (2진법)</option>
            <option value="8">Octal (8진법)</option>
            <option value="10">Decimal (10진법)</option>
            <option value="16">Hexadecimal (16진법)</option>
          </select>
        </div>
      </div>
      
      <button onClick={convertBase} style={styles.button}>
        변환
      </button>
      <div className="result" dangerouslySetInnerHTML={{ __html: result }} style={styles.resultContainer}></div>
    </div>
  );
}

const styles = {
  container: {
    maxWidth: '600px',
    margin: '10% auto',
    padding: '20px',
    backgroundColor: '#f8f9fa',
    borderRadius: '10px',
    boxShadow: '0 4px 10px rgba(0, 0, 0, 0.3)',
  },
  title: {
    textAlign: 'center',
    fontSize: '2rem',
    color: '#343a40',
    marginBottom: '10px',
  },
  description: {
    textAlign: 'center',
    color: '#6c757d',
    marginBottom: '20px',
  },
  input: {
    width: '100%',
    padding: '10px',
    margin: '10px 0',
    fontSize: '1.2rem',
    borderRadius: '5px',
    border: '1px solid #ced4da',
  },
  selectContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    flexWrap: 'wrap',  // Allows wrapping
    gap: '10px',       // Space between items
    marginBottom: '20px',
  },
  label: {
    display: 'block',
    marginBottom: '5px',
    fontSize: '1rem',
    color: '#495057',
  },
  select: {
    padding: '10px',
    fontSize: '1.1rem',
    borderRadius: '5px',
    border: '1px solid #ced4da',
    backgroundColor: '#fff',
    width: '180px',
    flex: '1 1 45%',  // Ensures each select box takes up ~45% of the container width
  },
  button: {
    display: 'block',
    width: '100%',
    padding: '10px',
    fontSize: '1.2rem',
    color: '#fff',
    backgroundColor: '#007bd0',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    transition: 'background-color 0.3s ease',
  },
  buttonHover: {
    backgroundColor: '#0056b3',
  },
  resultContainer: {
    marginTop: '20px',
    padding: '15px',
    backgroundColor: '#f1f3f5',
    borderRadius: '8px',
    border: '1px solid #adb5bd',
    fontSize: '1.1rem',
  },
};

export default Converter;
