import React, { useState, useRef, useEffect } from 'react';
import A1 from './A1.jpg';
import A2 from './A2.jpg';

function ImageComparison() {
  const [image1, setImage1] = useState(A1);
  const [image2, setImage2] = useState(A2);
  const [image1Name, setImage1Name] = useState('A1.jpg');
  const [image2Name, setImage2Name] = useState('A2.jpg');
  const [tolerance, setTolerance] = useState(10);
  const [previousTolerance, setPreviousTolerance] = useState(10);
  const diffCanvasRef = useRef(null);
  const diffSectionRef = useRef(null);

  const handleFileChange = (e, setImage, setImageName) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => setImage(reader.result);
      reader.readAsDataURL(file);
      setImageName(file.name);
    }
  };

  const loadImageToArray = (src, canvas) => {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.crossOrigin = 'anonymous';
      img.src = src;
      img.onload = () => {
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0);
        const imageData = ctx.getImageData(0, 0, img.width, img.height);

        const pixelArray = [];
        for (let x = 0; x < img.width; x++) {
          pixelArray[x] = [];
          for (let y = 0; y < img.height; y++) {
            const index = (y * img.width + x) * 4;
            pixelArray[x][y] = [
              imageData.data[index],     // R
              imageData.data[index + 1], // G
              imageData.data[index + 2], // B
              imageData.data[index + 3], // A
            ];
          }
        }
        resolve(pixelArray);
      };
      img.onerror = reject;
    });
  };

  const compareImages = async () => {
    if (!image1 || !image2) {
      return;
    }

    const canvas1 = document.createElement('canvas');
    const canvas2 = document.createElement('canvas');
    const diffCanvas = diffCanvasRef.current;
    const diffCtx = diffCanvas.getContext('2d');

    try {
      const imageArray1 = await loadImageToArray(image1, canvas1);
      const imageArray2 = await loadImageToArray(image2, canvas2);

      const originalWidth = Math.max(canvas1.width, canvas2.width);
      const originalHeight = Math.max(canvas1.height, canvas2.height);

      const resultContainer = diffSectionRef.current;
      const containerWidth = resultContainer.clientWidth;
      const containerHeight = resultContainer.clientHeight;

      const originalAspectRatio = originalWidth / originalHeight;
      const containerAspectRatio = containerWidth / containerHeight;

      let drawWidth, drawHeight;
      if (originalAspectRatio > containerAspectRatio) {
        drawWidth = containerWidth;
        drawHeight = containerWidth / originalAspectRatio;
      } else {
        drawHeight = containerHeight;
        drawWidth = containerHeight * originalAspectRatio;
      }

      diffCanvas.width = containerWidth;
      diffCanvas.height = containerHeight;

      diffCtx.clearRect(0, 0, diffCanvas.width, diffCanvas.height);

      const offsetX = (containerWidth - drawWidth) / 2;
      const offsetY = (containerHeight - drawHeight) / 2;

      const diffImageData = diffCtx.createImageData(originalWidth, originalHeight);
      for (let x = 0; x < originalWidth; x++) {
        for (let y = 0; y < originalHeight; y++) {
          const index = (y * originalWidth + x) * 4;
          const pixel1 = imageArray1[x]?.[y] || [0, 0, 0, 0];
          const pixel2 = imageArray2[x]?.[y] || [0, 0, 0, 0];

          if (
            Math.abs(pixel1[0] - pixel2[0]) > tolerance ||
            Math.abs(pixel1[1] - pixel2[1]) > tolerance ||
            Math.abs(pixel1[2] - pixel2[2]) > tolerance ||
            Math.abs(pixel1[3] - pixel2[3]) > tolerance
          ) {
            diffImageData.data[index] = 255;
            diffImageData.data[index + 1] = 0;
            diffImageData.data[index + 2] = 0;
            diffImageData.data[index + 3] = 255;
          } else {
            diffImageData.data[index] = pixel1[0];
            diffImageData.data[index + 1] = pixel1[1];
            diffImageData.data[index + 2] = pixel1[2];
            diffImageData.data[index + 3] = pixel1[3];
          }
        }
      }

      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = originalWidth;
      tempCanvas.height = originalHeight;
      tempCanvas.getContext('2d').putImageData(diffImageData, 0, 0);

      diffCtx.drawImage(tempCanvas, 0, 0, originalWidth, originalHeight, offsetX, offsetY, drawWidth, drawHeight);
    } catch (error) {
      console.error('이미지 데이터를 처리하는 중 오류 발생:', error);
    }
  };

  useEffect(() => {
    const interval = setInterval(() => {
      if (previousTolerance !== tolerance) {
        setPreviousTolerance(tolerance);
        compareImages();
      }
    }, 2000);

    return () => clearInterval(interval);
  }, [tolerance, previousTolerance]);

  useEffect(() => {
    const handleWheel = (event) => {
      event.preventDefault();
      if (event.deltaY < 0) {
        setTolerance((prev) => Math.min(prev + 1, 100));
      } else {
        setTolerance((prev) => Math.max(prev - 1, 0));
      }
    };

    const diffSection = diffSectionRef.current;
    if (diffSection) {
      diffSection.addEventListener('wheel', handleWheel);
    }

    return () => {
      if (diffSection) {
        diffSection.removeEventListener('wheel', handleWheel);
      }
    };
  }, []);

  // 윈도우 크기 변경 시 이미지 다시 비교
  useEffect(() => {
    const handleResize = () => {
      compareImages();
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    compareImages();
  }, []);

  return (
    <div>
      <style>{`
        .title {
          margin-bottom: 20px;
          font-size: 24px;
          font-weight: bold;
        }
        .image-container {
          display: flex;
          justify-content: center;
          gap: 20px;
          position: relative;
          margin-bottom: 20px;
        }
        .image-wrapper {
          position: relative;
        }
        .file-input-container {
          position: absolute;
          top: -30px;
          padding: 5px;
          border-radius: 4px;
        }
        .file-label {
          cursor: pointer;
          background: #fff;
          padding: 5px;
          border-radius: 4px;
          border: 1px solid #ccc;
        }
        .image {
          max-width: 70%;
          height: auto;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
        .tolerance-container {
          margin-top: 20px;
          margin-bottom: 20px;
        }
        .result-container {
          margin-top: 20px;
          border: 1px solid #000;

          display: inline-block;
        }
        canvas {
          display: block;
          margin: 0 auto;
        }
      `}</style>

      <div className="container">
        <div className="title">
          <h1>Image Comparison Tool</h1>
        </div>

        <div className="image-container">
          <div className="image-wrapper">
            <div className="file-input-container">
              <input
                type="file"
                accept="image/*"
                onChange={(e) => handleFileChange(e, setImage1, setImage1Name)}
                style={{ display: 'none' }}
                id="fileInput1"
              />
              <label htmlFor="fileInput1" className="file-label">
                {image1Name}
              </label>
            </div>
            <img src={image1} alt="First" className="image" />
          </div>

          <div className="image-wrapper">
            <div className="file-input-container">
              <input
                type="file"
                accept="image/*"
                onChange={(e) => handleFileChange(e, setImage2, setImage2Name)}
                style={{ display: 'none' }}
                id="fileInput2"
              />
              <label htmlFor="fileInput2" className="file-label">
                {image2Name}
              </label>
            </div>
            <img src={image2} alt="Second" className="image" />
          </div>
        </div>

        <div className="tolerance-container">
          <label>Tolerance: {tolerance}</label>
          <input
            type="range"
            min="0"
            max="100"
            value={tolerance}
            onChange={(e) => setTolerance(parseInt(e.target.value))}
          />
        </div>
        
        <button onClick={compareImages}>Compare Images</button>

        <div className="result-container" ref={diffSectionRef}>
          <p>Tolerance: {tolerance}</p>
          <canvas ref={diffCanvasRef}></canvas>
        </div>
      </div>
    </div>
  );
}

export default ImageComparison;
