import React, { useState } from 'react';
import myStyle from './NavDir.module.scss';

// 임의의 파일 및 폴더 데이터 구조


function NavDir({fileData , addViewList} ) {
  // 각 폴더의 열림/닫힘 상태를 저장하는 state를 생성
  const [openFolders, setOpenFolders] = useState({});

  // 아이템 클릭 시 동작
  function handleClick(item) {
    if (item.data.Type === 'file') {
      // 파일 : 주소 이동
      window.location.replace(`/myBlog/${item.data.path}`);
    } else if (item.data.Type === 'folder' && item.children && item.children.length > 0) {
      // 폴더 : 열림/닫힘 상태를 토글
      setOpenFolders(prevState => ({
        ...prevState,
        [item.data.label]: !prevState[item.data.label] // 토글 기능
      }));
    }
  }

  // 파일 트리를 렌더링하는 함수
  function renderFileTree(data, level = 1) {
    return data.map((item, index) => (
      <div key={index}>
        {/* 폴더나 파일 클릭 시 handleClick 호출 */}
        <div
          className={myStyle.lineDir}
          style={{ paddingLeft: `${level * 20}px` }}
          onClick={() => handleClick(item)}
        >
          <img src="/icons/folder-icon.png" alt="@" className={myStyle.icon} />
          <div>{item.data.Type} {item.data.label}</div>
        </div>
        
        {/* 폴더가 열렸으면 자식 요소를 렌더링 */}
        {item.data.Type === 'folder' && openFolders[item.data.label] && (
          <div className={myStyle.children}>
            {renderFileTree(item.children, level + 1)}
          </div>
        )}
      </div>
    ));
  }
  const handleAddItem = () => {
    const newItem = { title: 'test', link: '/test' };
    addViewList(newItem); 
  };

  return (
    <div className={myStyle.container}>
      {/* 상단 버튼 */}
      <div className={myStyle.header}>
        <div className={myStyle.buttons}>
          <button className={myStyle.button}>검색</button>
          <button className={myStyle.button}>폴더+</button>
          <button className={myStyle.button}>파일+</button>
        </div>
      </div>
      {/* <button onClick={handleAddItem}>NavDir에서 새로운 항목 추가</button> */}
      {/* 파일 목록 */}
      <div className={myStyle.fileList}>
        {renderFileTree(fileData)}
      </div>
    </div>
  );
}

export default NavDir;
