import React, { useState , useEffect} from 'react';
import {  Outlet , Link } from 'react-router-dom';

import myStyle from './MainBlog.module.scss';
import NavDir from './NavDir'; 



function MainBlog( {initialNodes})  {


  const [viewLists, setViewList] = useState([
    {title: 'mk test (X)',  link: '/blog/mkTest'  } ,
    {title: 'memoChart',  link: '/blog/memoChart'  } 
  ]);

  // viewLists에 새 항목 추가 함수
  const addViewList = (newItem) => {
    setViewList((prevLists) => [...prevLists, newItem]);
  };

  // 특정 title을 가진 항목 삭제 함수
  const removeViewList = (titleToRemove) => {
    setViewList((prevLists) =>
      prevLists.filter((item) => item.title !== titleToRemove)
    );
  };

  return (
    <div className={myStyle.container }>

      <div className={myStyle.header }>
        <div className={myStyle.tabs}>
          {/* 페이지 파일 */}
          {viewLists.map((item, index) => (
          <Link to={item.link} key={index} className={myStyle.tab}>
              {item.title} 
          </Link>
        ))}
        </div>
      </div>

      <div className={myStyle.content }>
        <div className={myStyle.content1}>
          <Outlet/> 
        </div>

        <div className={myStyle.content2}>
          <NavDir fileData={initialNodes}  addViewList={addViewList} />
        </div>
      </div>

    </div>
  );
}

export default MainBlog;
