import logo from './logo.svg';
import './App.css';
import { Router , Routes, Route } from 'react-router-dom';
import Header from './components/common/header/Haeder';
import Nav from './components/common/nav/NavSide';
import MainBlog from './components/common/main/MainBlog';
import FlowChart from './components/common/main/FlowChart';
import MkViewer from './components/common/main/MkViewer';
import TestAjax from './components/common/main/test/TestAjax'
import React, { useState, useEffect } from 'react';
import ImgComparison from './etc/imgCompare/ImgComparison'
import ConverterBase from './etc/convertBase/ConverterBase'


function TestView({ testDatas , testDatas1 ,testDatas2 }) {
  return (
    <div>
      <hr></hr>
      <div>
          {testDatas ? // JSON 데이터를 출력
          (<div>{JSON.stringify(testDatas)}</div> ) : 
          (<p>Loading...</p> )}
        </div>
        <hr></hr>
        <div>
          {testDatas1.map(subData => (
            <div key={subData.id}>
                <h1>{subData.title}</h1>
                <p>count: {subData.count}</p>
                <p>float: {subData.float}</p>
                <p>url: {subData.url}</p>
                <p>color: {subData.color.join(" , ")}</p>
              {/* 다른 속성들도 출력 */}
            </div>
          ))}
        </div>
        <hr></hr>
        <div>
          {testDatas2.map((subData) => (
            <div key={subData.id}>
                <h1>{subData.title}</h1>
                <p>count: {subData.count}</p>
                <p>float: {subData.float}</p>
                <p>url: {subData.url}</p>
                <p>color: {subData.color.join(" , ")}</p>
              {/* 다른 속성들도 출력 */}
            </div>
          ))}
        </div>
        <hr></hr>
    </div>
  );
}


function App() {

  const mdFilePath = '/myBlog/components/blog/mkList/mkTestJs.md';

  

  const [initialNodes, setInitialNodes] = useState(null);
  const [data, setData] = useState(null); // 데이터를 상태로 관리


  const fileData = [
    {data:
      {Type:'folder' ,path:'mkTest', label: '1'},
        children: [{ data:{Type:'folder' ,path:'mkTest', label: '1-1'} , 
                            children: [{ data:{Type:'folder' ,path:'mkTest', label: '1-1-1'}}, 
                                        { data:{Type:'file' ,path:'mkTest', label: '1-1-2'}}]
                    } ,
                    { data:{Type:'file' ,path:'mkTest', label: '1-2'}}] },

    {data:{Type:'folder' ,path:'mkTest', label: '2'}, children: [{ data:{Type:'file' ,path:'mkTest', label: '2-1'}}, { data:{Type:'file' ,path:'mkTest', label: '2-2'}}] },
    {data:{Type:'folder' ,path:'mkTest', label: '3'}, children: [{ data:{Type:'file' ,path:'mkTest', label: '3-1'}}] },
    {data:{Type:'folder' ,path:'mkTest', label: '4'}, children: [{ data:{Type:'file' ,path:'mkTest', label: '4-1'}}] },
  ];

  const initialEdges = [
    { id: 'e1-2', source: 'ts1', target: '2' },
    { id: 'e1-3', source: '4', target: '3' },
    { id: 'e1-4', source: '4', target: '2' }
  
  ];
  

  /* 로그인 체크 */
  const [emailCheck,setEmailCheck] = useState();

  /* 로컬스토리지 체크 */
  function loginCheck(){
    let getLocalEmail = localStorage.getItem("emailCheck");
    //로컬 스토리지에 emailCheck 있다면 setEmailCheck을 true / 없다면 false
    if(getLocalEmail){
      setEmailCheck(true)
    }else{
      setEmailCheck(false)
    }
  }

  const processTreeNode = (node, parentIdx = '', parentTitle = '') => {
    // 현재 노드의 idx와 title을 부모에서 전달받은 값과 결합
    const currentIdx = parentIdx ? `${parentIdx}-${node.idx}` : `${node.idx}`;
    const currentTitle = parentTitle ? `${parentTitle}/${node.title}` : `${node.title}`;
  
    // 파일이거나 자식이 없는 경우 현재 idx와 title만 반환
    if (!node.children || node.children.length === 0) {
      return [{ idx: currentIdx, title: currentTitle , type: node.type, content: node.content }];
    }
  
    // 자식이 있을 경우 재귀적으로 순회하여 모든 자식 노드 처리
    let result = [];
    node.children.forEach((child) => {
      result = result.concat(processTreeNode(child, currentIdx, currentTitle));
    });
  
    return result;
  };
  
  // 외부 JSON 데이터를 불러오는 함수
  const fetchInitialNodes = async () => {
    try {
      const response = await fetch('https://unknowntie.github.io/myblog_back_Public/contents/mdList.json');
      if (!response.ok) {
        throw new Error('데이터를 불러오는 중 오류 발생');
      }
      const result = await response.json();
      const data = result.flatMap(node => processTreeNode(node));
      setInitialNodes(data); // JSON 데이터를 initialNodes로 설정
      setData(result);
    } catch (error) {
      console.error('JSON 데이터를 불러오는 중 오류 발생:', error);
    }
  };

  // 컴포넌트가 마운트될 때 데이터를 불러옴
  useEffect(() => {
    fetchInitialNodes();
  }, []);


  if (!data) {
    return <div>Loading...</div>; // 데이터가 아직 로드되지 않았을 때
  }

  return (
    <div  className="App">
      <div className="container">
        <header  className="header"><Header/></header>
        <div className="content">
          <nav className="content1">
            <Nav/>
          </nav>  
          {/* <ul>
            {initialNodes.map((item, index) => (
              <li key={index}>
                <strong>Idx:</strong> {item.idx} <br />
                <strong>Title:</strong> {item.title} <br />
                <strong>Type:</strong> {item.type} <br />
                <strong>Content:</strong> {item.content || 'No content'}
              </li>
            ))}
          </ul> */}
          <div className="content2">
              <Routes>
                <Route path="/blog" element={<MainBlog initialNodes={fileData}/>}>
                {/* 자료 출력 위치 */}
                  <Route path="memoChart" element={<FlowChart initialNodes={initialNodes} initialEdges={initialEdges} /> } />
                    {/* 폴더링 볼 수 있게 */}
                  <Route path="mkTest" element={<MkViewer mdPath={mdFilePath}/>} />
                </Route>
                <Route path="/etc/imgCompare/ImgComparison" element={<ImgComparison mdPath={mdFilePath}/>} />
                <Route path="/etc/convertBase/ConverterBase" element={<ConverterBase mdPath={mdFilePath}/>} />
                <Route path="*" element={<TestAjax />} />
              </Routes> 
          </div>
        </div>
      </div>
    </div>
  );
  
}


export default App;

